* {
  box-sizing: border-box;
  font-family: "Dosis", sans-serif;
  margin: 0;
  padding: 0;
}

.navbar {
  height: 50px;
  background-color: #003580;
  display: flex;
  justify-content: center;
}
.navbar .navContainer {
  width: 100%;
  max-width: 1024px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.navbar .navContainer .logo {
  font-weight: 500;
}
.navbar .navContainer .navItems .navButton {
  margin-left: 20px;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  color: #003580;
  background-color: white;
}/*# sourceMappingURL=Navbar.css.map */