@import "../../STYLING/colors";
@import "../../STYLING/global_rules";
//
.featured{
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    .featuredItem{
        position: relative;
        color: white;
        border-radius: 10px;
        overflow: hidden;
        height: 250px;
        .featuredImg {
            @include samewh(100%);
            object-fit: cover;
        }
        .featuredTitles{
            position: absolute;
            bottom: 20px;
            left: 20px;
        }
    }
}